import { supabase, isSupabaseConfigured } from './supabase';

// ==========================================
// LIVE SESSIONS - Cross-device sync with offline fallback
// ==========================================

export interface LiveSession {
    id: string; // Format: "machineName_shift_date"
    machine_name: string;
    operator_name: string;
    order_number: string;
    product: string;
    batch_number: string;
    shift: string;
    session_date: string;
    is_locked: boolean;
    total_waste?: number;
    total_downtime?: number;
    // JSON-encoded entries for cross-device sync
    entries_json?: string;
    created_at?: string;
    updated_at?: string;
}

// Entry types for cross-device sync
export interface SyncedWasteEntry {
    id: string;
    waste: number;
    wasteType: string;
    timestamp: string;
}

export interface SyncedDowntimeEntry {
    id: string;
    downtime: number;
    downtimeReason: string;
    timestamp: string;
}

export interface SyncedSpeedEntry {
    id: string;
    speed: number;
    timestamp: string;
}

export interface SyncedSachetMassEntry {
    id: string;
    mass: number;
    timestamp: string;
}

export interface SyncedLooseCasesEntry {
    id: string;
    batchNumber: string;
    cases: number;
    timestamp: string;
}

export interface SyncedPalletScanEntry {
    id: string;
    qrCode: string;
    batchNumber: string;
    palletNumber: string;
    casesCount: number;
    timestamp: string;
    ignored?: boolean;
}

export interface SyncedEntries {
    wasteEntries: SyncedWasteEntry[];
    downtimeEntries: SyncedDowntimeEntry[];
    speedEntries: SyncedSpeedEntry[];
    sachetMassEntries: SyncedSachetMassEntry[];
    looseCasesEntries: SyncedLooseCasesEntry[];
    palletScanEntries: SyncedPalletScanEntry[];
}

// Generate session ID from components
export const getLiveSessionId = (machineName: string, shift: string, date: string): string => {
    return `${machineName}_${shift}_${date}`;
};

// Cache for active sessions (used when offline or for quick lookups)
let sessionsCache: Map<string, LiveSession> = new Map();
let lastFetchTime: number = 0;
const CACHE_TTL_MS = 30000; // 30 seconds cache

// ==========================================
// UPSERT SESSION (Save/Update)
// ==========================================
export const upsertLiveSession = async (session: {
    machineName: string;
    operatorName: string;
    orderNumber: string;
    product: string;
    batchNumber: string;
    shift: string;
    date: string;
    locked: boolean;
    totalWaste?: number;
    totalDowntime?: number;
}): Promise<boolean> => {
    const sessionId = getLiveSessionId(session.machineName, session.shift, session.date);

    const liveSession: LiveSession = {
        id: sessionId,
        machine_name: session.machineName,
        operator_name: session.operatorName,
        order_number: session.orderNumber,
        product: session.product,
        batch_number: session.batchNumber,
        shift: session.shift,
        session_date: session.date,
        is_locked: session.locked,
        total_waste: session.totalWaste || 0,
        total_downtime: session.totalDowntime || 0,
        updated_at: new Date().toISOString(),
    };

    // Always update local cache first
    sessionsCache.set(sessionId, liveSession);

    // Try to sync to Supabase
    if (isSupabaseConfigured) {
        try {
            const { error } = await supabase
                .from('live_sessions')
                .upsert({
                    id: sessionId,
                    machine_name: session.machineName,
                    operator_name: session.operatorName,
                    order_number: session.orderNumber,
                    product: session.product,
                    batch_number: session.batchNumber,
                    shift: session.shift,
                    session_date: session.date,
                    is_locked: session.locked,
                    total_waste: session.totalWaste || 0,
                    total_downtime: session.totalDowntime || 0,
                    updated_at: new Date().toISOString(),
                }, { onConflict: 'id' });

            if (error) {
                console.error('Failed to upsert live session:', error.message);
                return false;
            }
            console.log('âœ… Live session synced to Supabase:', sessionId);
            return true;
        } catch (e) {
            console.error('Failed to upsert live session:', e);
            return false;
        }
    }

    // Offline - cached locally, will sync later
    return true;
};

// ==========================================
// DELETE SESSION (When submitted or cleared)
// ==========================================
export const deleteLiveSession = async (
    machineName: string,
    shift: string,
    date: string
): Promise<boolean> => {
    const sessionId = getLiveSessionId(machineName, shift, date);

    // Remove from local cache
    sessionsCache.delete(sessionId);

    // Try to delete from Supabase
    if (isSupabaseConfigured) {
        try {
            const { error } = await supabase
                .from('live_sessions')
                .delete()
                .eq('id', sessionId);

            if (error) {
                console.error('Failed to delete live session:', error.message);
                return false;
            }
            return true;
        } catch (e) {
            console.error('Failed to delete live session:', e);
            return false;
        }
    }

    return true;
};

// ==========================================
// FETCH ACTIVE SESSIONS (For Dashboard)
// ==========================================
export const fetchActiveSessions = async (): Promise<LiveSession[]> => {
    const now = Date.now();
    const today = new Date().toISOString().split('T')[0];

    // Return cache if fresh and online fetch fails
    const shouldUseCache = now - lastFetchTime < CACHE_TTL_MS;

    if (isSupabaseConfigured) {
        try {
            const { data, error } = await supabase
                .from('live_sessions')
                .select('*')
                .eq('session_date', today)
                .eq('is_locked', true);

            if (error) {
                console.error('Failed to fetch live sessions:', error.message);
                // Fall back to cache
                return Array.from(sessionsCache.values()).filter(
                    s => s.session_date === today && s.is_locked
                );
            }

            // Update cache with fetched data
            sessionsCache.clear();
            (data || []).forEach(session => {
                sessionsCache.set(session.id, session);
            });
            lastFetchTime = now;

            console.log('ðŸ“‹ Fetched active sessions from Supabase:', (data || []).length, data?.map(s => s.machine_name));
            return data || [];
        } catch (e) {
            console.error('Failed to fetch live sessions:', e);
            // Fall back to cache
            return Array.from(sessionsCache.values()).filter(
                s => s.session_date === today && s.is_locked
            );
        }
    }

    // Offline - return cached sessions
    if (shouldUseCache) {
        return Array.from(sessionsCache.values()).filter(
            s => s.session_date === today && s.is_locked
        );
    }

    return [];
};

// ==========================================
// FETCH SESSION BY MACHINE NAME
// ==========================================
export const fetchLiveSessionByMachine = async (
    machineName: string,
    shift: string,
    date: string
): Promise<LiveSession | null> => {
    const sessionId = getLiveSessionId(machineName, shift, date);

    // Check cache first
    if (sessionsCache.has(sessionId)) {
        return sessionsCache.get(sessionId) || null;
    }

    if (!isSupabaseConfigured) {
        return null;
    }

    try {
        const { data, error } = await supabase
            .from('live_sessions')
            .select('*')
            .eq('id', sessionId)
            .eq('is_locked', true)
            .maybeSingle();

        if (error) {
            console.error('Failed to fetch session:', error.message);
            return null;
        }

        if (data) {
            // Update cache
            sessionsCache.set(sessionId, data);
            console.log('ðŸ“‹ Restored session from Supabase:', machineName);
        }

        return data;
    } catch (e) {
        console.error('Failed to fetch session by machine:', e);
        return null;
    }
};

// ==========================================
// GET ACTIVE SUB-MACHINES (Replacement for localStorage check)
// ==========================================
export const getActiveSubMachinesFromSupabase = async (
    parentMachineName: string,
    subMachineCount: number
): Promise<Set<number>> => {
    const activeSet = new Set<number>();
    const sessions = await fetchActiveSessions();

    for (let i = 1; i <= subMachineCount; i++) {
        const fullName = `${parentMachineName} - Machine ${i}`;
        const isActive = sessions.some(s => s.machine_name === fullName && s.is_locked);
        if (isActive) {
            activeSet.add(i);
        }
    }

    return activeSet;
};

// ==========================================
// SUBSCRIBE TO SESSION CHANGES (Real-time)
// ==========================================
export const subscribeToSessionChanges = (
    onUpdate: (sessions: LiveSession[]) => void
) => {
    if (!isSupabaseConfigured) {
        return () => { }; // No-op unsubscribe
    }

    const today = new Date().toISOString().split('T')[0];

    const channel = supabase
        .channel('live-sessions-changes')
        .on(
            'postgres_changes',
            { event: '*', schema: 'public', table: 'live_sessions' },
            async (payload) => {
                console.log('ðŸ”„ Real-time session update received:', payload.eventType);
                // Refetch all sessions on any change
                const sessions = await fetchActiveSessions();
                onUpdate(sessions);
            }
        )
        .subscribe((status) => {
            console.log('ðŸ“¡ Session subscription status:', status);
        });

    return () => {
        supabase.removeChannel(channel);
    };
};

// ==========================================
// SYNC PENDING SESSIONS (On reconnection)
// ==========================================
export const syncPendingSessions = async (): Promise<void> => {
    if (!isSupabaseConfigured) return;

    // Any sessions in cache that might not be synced yet
    for (const session of Array.from(sessionsCache.values())) {
        try {
            await supabase
                .from('live_sessions')
                .upsert({
                    id: session.id,
                    machine_name: session.machine_name,
                    operator_name: session.operator_name,
                    order_number: session.order_number,
                    product: session.product,
                    batch_number: session.batch_number,
                    shift: session.shift,
                    session_date: session.session_date,
                    is_locked: session.is_locked,
                    updated_at: new Date().toISOString(),
                }, { onConflict: 'id' });
        } catch (e) {
            console.error('Failed to sync session:', session.id, e);
        }
    }
};

// ==========================================
// ACTIVITY FEED - Real-time machine events
// ==========================================

export type ActivityEventType =
    | 'machine_start'
    | 'machine_pause'
    | 'machine_resume'
    | 'waste_recorded'
    | 'downtime_recorded'
    | 'pallet_scanned'
    | 'cases_added'
    | 'sachet_mass_added'
    | 'shift_submitted'
    | 'speed_recorded';

export interface ActivityEvent {
    id: string;
    type: ActivityEventType;
    machine_name: string;
    operator_name?: string;
    message: string;
    details?: string;
    timestamp: Date;
}

// In-memory activity log (most recent first)
let activityLog: ActivityEvent[] = [];
const MAX_ACTIVITY_ITEMS = 50;
let activityListeners: ((events: ActivityEvent[]) => void)[] = [];

// Add an activity event
export const addActivityEvent = (event: Omit<ActivityEvent, 'id' | 'timestamp'>) => {
    const newEvent: ActivityEvent = {
        ...event,
        id: `${Date.now()}-${Math.random().toString(36).substr(2, 9)}`,
        timestamp: new Date(),
    };

    activityLog = [newEvent, ...activityLog].slice(0, MAX_ACTIVITY_ITEMS);

    // Notify listeners
    activityListeners.forEach(listener => listener([...activityLog]));

    // Also broadcast to Supabase for cross-device sync
    if (isSupabaseConfigured) {
        broadcastActivityEvent(newEvent);
    }
};

// Broadcast activity to other clients via Supabase Realtime
const broadcastActivityEvent = async (event: ActivityEvent) => {
    try {
        const channel = supabase.channel('activity-broadcast');
        await channel.send({
            type: 'broadcast',
            event: 'activity',
            payload: {
                ...event,
                timestamp: event.timestamp.toISOString(),
            },
        });
    } catch (e) {
        // Silently fail - activity feed is non-critical
    }
};

// Subscribe to activity events
export const subscribeToActivityFeed = (
    onUpdate: (events: ActivityEvent[]) => void
) => {
    // Add listener
    activityListeners.push(onUpdate);

    // Send current log immediately
    onUpdate([...activityLog]);

    // Subscribe to Supabase broadcast for cross-device events
    let channel: ReturnType<typeof supabase.channel> | null = null;

    if (isSupabaseConfigured) {
        channel = supabase
            .channel('activity-broadcast')
            .on('broadcast', { event: 'activity' }, (payload) => {
                const eventData = payload.payload as ActivityEvent & { timestamp: string };
                const event: ActivityEvent = {
                    ...eventData,
                    timestamp: new Date(eventData.timestamp),
                };

                // Avoid duplicates
                if (!activityLog.some(e => e.id === event.id)) {
                    activityLog = [event, ...activityLog].slice(0, MAX_ACTIVITY_ITEMS);
                    activityListeners.forEach(listener => listener([...activityLog]));
                }
            })
            .subscribe();
    }

    // Return unsubscribe function
    return () => {
        activityListeners = activityListeners.filter(l => l !== onUpdate);
        if (channel) {
            supabase.removeChannel(channel);
        }
    };
};

// Get current activity log
export const getActivityLog = (): ActivityEvent[] => [...activityLog];

// Clear activity log
export const clearActivityLog = () => {
    activityLog = [];
    activityListeners.forEach(listener => listener([]));
};

// ==========================================
// ENTRY SYNC - Real-time cross-device sync
// ==========================================

// Sync all entries to Supabase for cross-device access
// Note: Requires entries_json column in live_sessions table
export const syncEntriesToSupabase = async (
    machineName: string,
    shift: string,
    date: string,
    entries: SyncedEntries
): Promise<boolean> => {
    if (!isSupabaseConfigured) return false;

    const sessionId = getLiveSessionId(machineName, shift, date);
    const entriesJson = JSON.stringify(entries);

    try {
        const { error } = await supabase
            .from('live_sessions')
            .update({
                entries_json: entriesJson,
                updated_at: new Date().toISOString(),
            })
            .eq('id', sessionId);

        if (error) {
            // Silently fail if entries_json column doesn't exist yet
            if (error.message.includes('entries_json') || error.message.includes('column')) {
                // Column doesn't exist - sync feature not available yet
                return false;
            }
            console.error('Failed to sync entries:', error.message);
            return false;
        }

        console.log('ðŸ“¦ Entries synced to Supabase:', sessionId);
        return true;
    } catch (e) {
        // Silently fail - sync feature may not be available
        return false;
    }
};

// Fetch entries from Supabase for a session
// Note: Requires entries_json column in live_sessions table
export const fetchEntriesFromSupabase = async (
    machineName: string,
    shift: string,
    date: string
): Promise<SyncedEntries | null> => {
    if (!isSupabaseConfigured) return null;

    const sessionId = getLiveSessionId(machineName, shift, date);

    try {
        const { data, error } = await supabase
            .from('live_sessions')
            .select('entries_json')
            .eq('id', sessionId)
            .maybeSingle();

        if (error) {
            // Silently fail if entries_json column doesn't exist yet
            if (error.message.includes('entries_json') || error.message.includes('column')) {
                return null;
            }
            console.error('Failed to fetch entries:', error.message);
            return null;
        }

        if (data?.entries_json) {
            const entries = JSON.parse(data.entries_json) as SyncedEntries;
            console.log('ðŸ“¦ Entries restored from Supabase:', sessionId);
            return entries;
        }

        return null;
    } catch (e) {
        // Silently fail - sync feature may not be available
        return null;
    }
};

// Subscribe to entry changes for real-time sync
export const subscribeToEntryChanges = (
    machineName: string,
    shift: string,
    date: string,
    onEntriesUpdate: (entries: SyncedEntries) => void
) => {
    if (!isSupabaseConfigured) {
        return () => { }; // No-op unsubscribe
    }

    const sessionId = getLiveSessionId(machineName, shift, date);

    const channel = supabase
        .channel(`entries-${sessionId}`)
        .on(
            'postgres_changes',
            {
                event: 'UPDATE',
                schema: 'public',
                table: 'live_sessions',
                filter: `id=eq.${sessionId}`
            },
            (payload) => {
                const newData = payload.new as { entries_json?: string };
                if (newData.entries_json) {
                    try {
                        const entries = JSON.parse(newData.entries_json) as SyncedEntries;
                        console.log('ðŸ”„ Real-time entries update received:', sessionId);
                        onEntriesUpdate(entries);
                    } catch (e) {
                        console.error('Failed to parse entries update:', e);
                    }
                }
            }
        )
        .subscribe((status) => {
            console.log('ðŸ“¡ Entry sync subscription status:', status, sessionId);
        });

    return () => {
        supabase.removeChannel(channel);
    };
};

// Helper to safely convert timestamp to ISO string
const toISOString = (timestamp: Date | string): string => {
    if (typeof timestamp === 'string') return timestamp;
    if (timestamp instanceof Date) return timestamp.toISOString();
    return new Date(timestamp).toISOString();
};

// Helper to convert local entries to synced format
export const convertToSyncedEntries = (
    wasteEntries: { id: string; waste: number; wasteType: string; timestamp: Date | string }[],
    downtimeEntries: { id: string; downtime: number; downtimeReason: string; timestamp: Date | string }[],
    speedEntries: { id: string; speed: number; timestamp: Date | string }[],
    sachetMassEntries: { id: string; mass: number; timestamp: Date | string }[],
    looseCasesEntries: { id: string; batchNumber: string; cases: number; timestamp: Date | string }[],
    palletScanEntries: { id: string; qrCode: string; batchNumber: string; palletNumber: string; casesCount: number; timestamp: Date | string; ignored?: boolean }[]
): SyncedEntries => {
    return {
        wasteEntries: wasteEntries.map(e => ({
            id: e.id,
            waste: e.waste,
            wasteType: e.wasteType,
            timestamp: toISOString(e.timestamp),
        })),
        downtimeEntries: downtimeEntries.map(e => ({
            id: e.id,
            downtime: e.downtime,
            downtimeReason: e.downtimeReason,
            timestamp: toISOString(e.timestamp),
        })),
        speedEntries: speedEntries.map(e => ({
            id: e.id,
            speed: e.speed,
            timestamp: toISOString(e.timestamp),
        })),
        sachetMassEntries: sachetMassEntries.map(e => ({
            id: e.id,
            mass: e.mass,
            timestamp: toISOString(e.timestamp),
        })),
        looseCasesEntries: looseCasesEntries.map(e => ({
            id: e.id,
            batchNumber: e.batchNumber,
            cases: e.cases,
            timestamp: toISOString(e.timestamp),
        })),
        palletScanEntries: palletScanEntries.map(e => ({
            id: e.id,
            qrCode: e.qrCode,
            batchNumber: e.batchNumber,
            palletNumber: e.palletNumber,
            casesCount: e.casesCount,
            timestamp: toISOString(e.timestamp),
            ignored: e.ignored,
        })),
    };
};

// Helper to convert synced entries back to local format
export const convertFromSyncedEntries = (synced: SyncedEntries) => {
    return {
        wasteEntries: synced.wasteEntries.map(e => ({
            id: e.id,
            waste: e.waste,
            wasteType: e.wasteType,
            timestamp: new Date(e.timestamp),
        })),
        downtimeEntries: synced.downtimeEntries.map(e => ({
            id: e.id,
            downtime: e.downtime,
            downtimeReason: e.downtimeReason,
            timestamp: new Date(e.timestamp),
        })),
        speedEntries: synced.speedEntries.map(e => ({
            id: e.id,
            speed: e.speed,
            timestamp: new Date(e.timestamp),
        })),
        sachetMassEntries: synced.sachetMassEntries.map(e => ({
            id: e.id,
            mass: e.mass,
            timestamp: new Date(e.timestamp),
        })),
        looseCasesEntries: synced.looseCasesEntries.map(e => ({
            id: e.id,
            batchNumber: e.batchNumber,
            cases: e.cases,
            timestamp: new Date(e.timestamp),
        })),
        palletScanEntries: synced.palletScanEntries.map(e => ({
            id: e.id,
            qrCode: e.qrCode,
            batchNumber: e.batchNumber,
            palletNumber: e.palletNumber,
            casesCount: e.casesCount,
            timestamp: new Date(e.timestamp),
            ignored: e.ignored,
        })),
    };
};
